<template>
	
	<div>
		 <router-view></router-view>
		 <div class="ui-content">
      <div class="c-top">
      	<el-button type="primary" size="mini" class='fr drafts'><router-link to='/statistics/MyCase/drafts'>草稿箱</router-link></el-button>

        <div class="fr ui-search">
          <i class="icon-search"></i>
          <input type="text" placeholder="案例名称" v-model="searchTitle">
          <button @click="getCaseDatas(0)">搜索</button>
        </div>
      </div>
      
      <div class="c-list">
        <ul class="ui-case-list">
          <li style="position: relative;">
            <div class="c-add-case" @click="addNewCase">
              <i></i>
                   <a>创建新案例</a>
            </div>
          </li>
          <li v-for="el in dataList">
            <div class="ui-top" :style="{backgroundImage: 'url(http://qdzy.internal-i-focusing.com/getImage?id=' + el._id + ')'}" @click="toDetail(el._id)">
              <div class="ui-time">{{formatData(el.createTime)}}</div>
              <div class="ui-name"><span class="pre-block" v-show='el.isPerfect'>精</span>{{el.caseTitle}}</div>
            </div>
            <!--如果是集团账户创建案例-->
            <div class="ui-bottom" v-show="el.userInfo.userLevel=='groupUser'">
	            <button class="_btn" @click="publish(el)">{{el.checkState==7?'已发布':'发布案例'}}</button>
	            <button class="_btn" @click="addDigest(el)" v-show="!el.isPerfect">加精</button>
	        </div>
                        
            <div v-show="el.userInfo.userLevel!='groupUser'">
            	<!--3个步骤的流程图-->
            	<div class="ui-bottom ui-pro-bar" v-show="el.checkState!=3 && el.checkState!=9">
		              <div class="w-3 ft-16">
		                <span class="txt-left">提交</span>
		                <span class="txt-center">审核中</span>
		                <span class="txt-right">发布</span>
		              </div>
		              <div class="w-2" style="position: relative;height: 30px;">
		                <p class="ui-hr"></p>
		                <span class="ui-pro bg-blur"></span>
		                <span class="ui-pro" :class="{'bg-blur':(el.checkState==2 || el.checkState==4 || el.checkState==5 || el.checkState==6 || el.checkState==7)}"></span>
		                <span class="ui-pro" :class="{'bg-blur':el.checkState==7}"></span>
		                <p class="ui-hr-2 fl" :class="{'bg-blur':(el.checkState==2 || el.checkState==4 || el.checkState==5 || el.checkState==6 || el.checkState==7)}"></p>
		                <p class="ui-hr-2 fl" :class="{'bg-blur':el.checkState==7}"></p>
		              </div>
		              <div class="w-3 ft-12 bar-hink">
		                <span class="txt-left">{{formatData(el.createTime)}}</span>
		                <span class="txt-center">{{el.checkPoints.province==0?'':formatData(el.checkPoints.province)}}</span>
		                <span class="txt-right">{{el.checkPoints.publish==0?'':formatData(el.checkPoints.publish)}}</span>
		              </div>
            	</div>
            <!--4个步骤的流程图-->
	            <div class="ui-bottom ui-pro-bar all-step-4" v-show="el.checkState==3 || el.checkState==9">
	              <div class="w-4 ft-16" style="position: relative;text-align: left;">
	                <span class="txt-left">提交</span>
	                <span class="txt-center">审核中</span>
	                <span class="txt-center"> <button class="txt-center bg-red btn-back">驳回</button>
	                 <div class="ui-back-reason">
	                   {{el.backReson}}                   
	                 </div>
	                </span>
	
	              </div>
	              <div class="" style="position: relative;padding: 0 20px;height: 20px;">
	                <p class="ui-hr"></p>
	                <span class="ui-pro bg-blur"></span>
	                <span class="ui-pro" :class="{'bg-blur':(el.checkState==2 || el.checkState==4 || el.checkState==5 || el.checkState==6 || el.checkState==3 || el.checkState==9)}"></span>
	                <span class="ui-pro" :class="{'bg-red':el.checkState==3 || el.checkState==9}"></span>
	                <p class="ui-hr-3 fl" :class="{'bg-blur':(el.checkState==2 || el.checkState==4 || el.checkState==5 || el.checkState==6 || el.checkState==3 || el.checkState==9)}"></p>
	                <p class="ui-hr-3 fl" :class="{'bg-red':el.checkState==3 || el.checkState==9}"></p>
	                <div class="bg-blur bar-btn-edit">
	                	<a @click="editorthis(el._id)">再次编辑</a>
	                </div>
	              </div>
	              <div class="w-4 ft-12 bar-hink">
	                <span class="txt-left">{{formatData(el.createTime)}}</span>
	                <span class="txt-center">{{el.checkPoints.province==0?'':formatData(el.checkPoints.province)}}</span>
	                <span class="txt-right">{{el.checkPoints.publish==0?'':formatData(el.checkPoints.publish)}}</span>
	              </div>
	            </div>
            </div>         
            
          </li>
        </ul>
      </div>
      <div class="ui-page" v-show="totalPage>5">
        <div class="block fr">
        <el-pagination
		      @current-change="handleCurrentChange"
		      :page-size="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
        </div>
      </div>
      </div>
      <pop></pop>
    </div>
    	
</template>
<script type="text/javascript">
	import $ from 'jquery'
	import pop from '../pop-up-box/pop-up';
	
    export default {
      name: 'MyCase',	
      data () {
        return {
        	domain:'http://qdzy.internal-i-focusing.com',
        	totalPage:0,
//          模拟数据
          dataList:[],
          userInfo:{
          	id:''
          },
          searchTitle:undefined,
          curPageNo:0
        }
      },
      components:{
    	pop
    },
      mounted:function(){
    	var _this=this;
    	
    		//获取所有案例数据
			_this.getCaseDatas=function (pageNo,userId){
				_this.curPageNo=pageNo;
				var _data={
					category: "Samples",
					conditions: {
						createUser:userId || _this.userInfo.id,
						caseTitle:_this.searchTitle==undefined?undefined:{$regex:_this.searchTitle},
						checkState: {$ne: 100}
					},
					filter: {caseImg: 0},
					pageNumber: pageNo,
					pageSize: 5,
					sort: {createTime: -1}
				},
				url=_this.domain+'/wif/data/query';
				$.ajax({
					type:"post",
					url:url,
					async:true,
					data:JSON.stringify(_data),
					success:function(jqXhr,statusCode,statusText){
						var rs=JSON.parse(statusText.responseText);
						_this.dataList=rs.list;
						_this.totalPage=rs.count;
						
					},
					error:function(jqXhr,statusCode,statusText){
//						alert(statusText)
					}
				});						
			};
    	
    			//获取用户信息
			getUserInfo();
			function getUserInfo(){
					$.ajax({
					type:"post",
					url:_this.domain+"/user/getCurrentUser",
					async:true,
					xhrFields:{
						withCredentials: true
					},				
					data:JSON.stringify({}),
					dataType:'json',
					success:function(res){	
						_this.userInfo.id=res.content._id;
						_this.getCaseDatas(0,res.content._id);
					},
					error:function(jqXhr,statusCode,statusText){
						console.log(statusCode)
					}
				})
			};
    	
			
    },
     methods:{
     	addNewCase:function(){
     		this.$router.replace({path:'/MyCase/editor'})
     	},
     	editorthis:function(id){
	      	  this.$router.replace({path:'/MyCase/editor',query:{caseId:id}})
	      },
    	handleCurrentChange:function(x){
    		var _this=this;
    		_this.getCaseDatas(x-1);
    	},
    	formatData:function(date){    
    		var _this=this;
    		var _this=new Date(date),fmt='yyyy-MM-dd';
    		 var o = {
		        "M+": _this.getMonth() + 1, //月份 
		        "d+": _this.getDate(), //日 
		        "h+": _this.getHours(), //小时 
		        "m+": _this.getMinutes(), //分 
		        "s+": _this.getSeconds(), //秒 
		        "q+": Math.floor((_this.getMonth() + 3) / 3), //季度 
		        "S": _this.getMilliseconds() //毫秒 
		    };
		    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
		    for (var k in o)
		    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		    return fmt;
    	},
    	toDetail:function(id){
    		this.$router.replace({path:'/casecase/case-detail',query:{id:id}})  		
    	},
    	//发布案例
    	publish:function(el){ 
    			var _this=this;
    			
    			if(el.checkState==7){
    				$('.shade').hide();
    				return
    				
    			}
    			$('.shade').fadeIn();
    			$('.shade .pop-is').text("确认发布案例？");
    			$('.shade .sure').unbind('click');//防止事件重复绑定
    			$('.shade .callOff').unbind('click')
    			$('.shade .sure').on('click',function(){//确认发布案例
							var _data={
								category:'Samples',
								filter: {caseImg: 0},
								content:{
									checkState:7
								},
								id:el._id								
							},
							url1=_this.domain+'/wif/data/modify';
							$.ajax({
								type:"post",
								url:url1,
								async:true,
								dataType: 'json',
								data:JSON.stringify(_data),
								success:function(res){
									$('.shade').fadeOut();
									//发布成功之后加10积分
									var addJf={
										category:'Statistics',
										content:{
											caseId:el._id,
											score:10,
											type:'pass',
											reason:'发布成功',
											district:el.userInfo.proAddress
										}	
									},
									url2=_this.domain+'/wif/data/create';
									$.ajax({
										type:"post",
										url:url2,
										data:JSON.stringify(addJf),
										async:true,
										success:function(res){
											_this.getCaseDatas(_this.curPageNo);
										},
										error:function(jqXhr,statusCode,statusText){
											alert(statusText)
										}
									});
								},
								error:function(jqXhr,statusCode,statusText){
									alert(jqXhr,statusCode,statusText)									
								}
							});
      		});
      		$('.shade .callOff').on('click',function(){//取消通过审核
      			   $('.shade').fadeOut();
      		});
    	},
    	//加精案例
    	addDigest:function(el){
    			var _this=this;    		
    		  $('.shade .sure').unbind('click');//防止事件重复绑定
    			$('.shade .callOff').unbind('click')//防止事件重复绑定
    			$('.shade').show();
    			$('.shade .pop-is').text("确认案例加精？");
    			$('.shade .sure').on('click',function(){//确认加精

							var addData={
								category:'Statistics',
								content:{
									caseId:el._id,
									score:20,
									type:'perfect',
									reason:'加精',
									district:el.userInfo.proAddress
								}								
							},
							url=_this.domain+'/wif/data/create';
							$.ajax({
								type:"post",
								url:url,
								data:JSON.stringify(addData),
								async:true,
								success:function(res){
									$('.shade').fadeOut();
									_this.getCaseDatas(_this.curPageNo);
								},
								error:function(jqXhr,statusCode,statusText){
									alert(statusText)
								}
							});

      		});
      		$('.shade .callOff').on('click',function(){//取消加精
      			   $('.shade').fadeOut();
      		});
    	},
    	
    	
    },
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
